.page {
  position: absolute;
  width: 100vw;
  overflow: hidden;
  background: #e0dad7;
  min-height: 100vh;
}

.page-contents {
  margin-left: 10vw;
  margin-top: 70px;
}

.visualization {
  position: fixed;
  top: 200px;
  width: 40vw;
  height: 600px;
  margin: 10px;
}

.visualization-fancy {
  @extend .visualization;
  transition: visibility 1s, opacity 0.5s cubic-bezier(0.25, 0.1, 0.25, 1.0);
}

.text-block {
  min-height: 300px;
}

.accompany-text-all {
  margin-top: 150px;
  margin-left: 47vw;
  padding: 40px;
  padding-top: 30px;
  padding-bottom: 900px;
  background: white;
  width: 27vw;
  font-family: inherit;


  h1 {
    // the title
    font-family: "Orelega One", cursive;
    padding-top: 80px;
    padding-left: 130px;
    padding-bottom: 100px;
    font-size: 45px;
    font-weight: 500;

    background-image: url('../../assets/ink-bleed.png');
    background-size: 100px 100px;
    background-repeat: no-repeat;
    background-position: 10px 80px;
  }

  p {
    font-size: 24px;
    margin-top: 30px;
  }

  h2 {
    margin-top: 100px;
  }
}

.caption {
  font-size: 18px;
  margin-top: 20px;
  margin-bottom: 20px;
  color: #454545;

  a {
    text-decoration: none;
    padding-bottom: 1px;
    border-bottom: 1px dotted #000;
    color: #454545;


    &:hover {
      color: #752929;
    }
  }
}

.news-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  overflow: scroll;
  max-height: 80vh;

  .news-card {
    background: white;
    padding: 20px;
    width: 250px;
    margin: 15px 15px;
  }
}

.source-link {
  text-decoration: none;
  padding-bottom: 1px;
  border-bottom: 1px dotted #000;
  color: #454545;

  &:hover {
    color: #752929;
  }
}
